* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #363636;
  font-family: 'Noto Sans KR', sans-serif;
}
body {
  background-color: #fcaa6f;
  /* 스크롤 방지 */
  -webkit-user-select: none; 
  -moz-user-select: none; 
  -ms-user-select: none; 
  user-select: none;
}
a {
  text-decoration: none;
}
input:focus {
  outline: none;
}
li {
  list-style-type: none;
}

.bodyContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 600px;
  min-height: 100vh;
  margin: auto;
  padding: 40px 30px;
  box-shadow: 0px 0px 10px 1px rgba(87,87,87,0.7);
  background-color: #fafafa;
}
.mainTitle {
  font-size: 43px;
  font-weight: 900;
  letter-spacing: 0.08em;
  padding: 10px;
  margin-bottom: 20px;
}
.jumbotron {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 600px;
  height: 350px;
  text-align: center;
  background-image: url('../public/img/fist.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
  margin-bottom: 30px;
}
.jumbotron>span {
  padding: 10px 30px;
  border: 5px solid #fd9348;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.514);
  color: #202020be;
  font-size: 23px;
  font-weight: 900;
  line-height: 1.6;
  margin-bottom: 10px;
}
.jumbotron>p {
  font-size: 14px;
  font-weight: 300;
}
.enBtn {
  padding: 5px 10px;
  background-color: #ffffff00;
  border: 1px solid #666666;
  border-radius: 4px;
  cursor: pointer;
}
.enBtn:hover {
  background-color: #fcaa6f;
  border-color: #fcaa6f;
  color: #ffffff;
}
.enterName {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.enterName>p {
  margin-bottom: 50px;
}
.enterName>input[type="submit"] {
  padding: 5px 10px;
  background-color: #ffffff;
  border: 1px solid #9b9b9b;
  border-radius: 6px;
  cursor: pointer;
}
.enterName>input[type="submit"]:hover {
  background-color: #fcaa6f;
  color: #ffffff;
  border: 1px solid #fcaa6f;
}
.nameInput {
  width: 80%;
  border: none;
  border-bottom: 1px solid #9b9b9b;
  background-color: #ffffff00;
  margin-bottom: 20px;
  padding: 5px;
  text-align: center;
}
.nameInput:focus{
  outline: none;
}
/* todo */
.ygTitle, .todoTitle {
  margin-bottom: 30px;
}
.closeBtn {
  position: absolute;
  top: 14px;
  right: 28px;
  cursor: pointer;
}
.closeBtn>div {
  position: absolute;
  border: 1px solid black;
  width: 1px;
  height: 22px;
}
.closeBtn>div:first-child {
  transform: rotate(45deg);
}
.closeBtn>div:last-child {
  transform: rotate(-45deg);
}
.todoList {
  margin-bottom: 50px;
}
.todoList>li {
  margin-bottom: 8px;
  list-style-type: disc;
}
.todoList>li:last-child {
  margin-bottom: 0;
}
.addInput {
  margin-bottom: 20px;
}
.addTodo, .addYear {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}
.addTodo>label, .addYear>label {
  margin-bottom: 10px;
}
.addInput {
  display: flex;

}
.todoInput {
  padding: 6px;
  border: none;
  border-bottom: 1px solid #888888;
  background-color: #ffffff00;
  margin-right: 12px;
  text-align: center;
}
.addInput>input[type="submit"] {
  padding: 3px 23px;
  background-color: #ffffff;
  border: none;
  border: 1px solid #888888;
  border-radius: 4px;
  cursor: pointer;
}
.clearBtn {
  padding: 5px 15px;
  background-color: #ffffff00;
  border: solid 1px #9b9b9b;
  border-radius: 7px;
  cursor: pointer;
}
.nextBtn {
  padding: 5px 9px;
  background-color: #ffffff00;
  border: 1px solid #9b9b9b;
  border-radius: 6px;
  cursor: pointer;
}
.nextBtn:hover {
  background-color: #fcaa6f;
  border-color: #fcaa6f;
  color: #ffffff;
}
/* Collect */
.collectTitle {
  margin-bottom: 50px;
}
.randomBanner {
  position: relative;
  display: flex;
  width: 600px;
  height: 150px;
  background-color: #fcaa6f56;
  text-align: center;
  margin-bottom: 70px;
}
.randomBanner>p {
  margin: auto;
  white-space: pre-wrap;
}
.bannerBtn {
  position: absolute;
  bottom: 5px;
  right: 5px;
  padding: 1px 5px;
  background-color: #ffffff00;
  border: 1px solid #353535;
  border-radius: 4px;
  cursor: pointer;
}
.chkContainer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.today {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #363636;
  padding: 10px 20px;
}
.today>h4 {
  font-size: 19px;
  text-align: center;
  margin-bottom: 20px;
}
.todayLi {
  margin-bottom: 30px;
  width: 50%;
}
.todayLi>li {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 12px;
  cursor: pointer;
}
.todayLi>li:last-child {
  margin-bottom: 0;
}
.checkBox {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 1px solid #363636;
  margin-right: 12px;
  cursor: pointer;
}
.chkIcon {
  display: none;
  position: absolute;
  top: -11px;
  left: -3px;
  width: 30px;
  height: 35px;
  font-size: 26px;
}
.trashIcon {
  position: absolute;
  right: 0;
  color: red;
}
.addBtn {
  padding: 4px 8px;
  margin-bottom: 10px;
  background-color: #ffffff00;
  border: 1px solid #777777;
  border-radius: 4px;
  cursor: pointer;
}
.addBtn:hover{
  background-color: #fcaa6f;
  color: #ffffff;
  border-color: #fcaa6f;
}
.lastAddModal {
  display: none;
  margin-bottom: 10px;
}
.lastAddModal.open {
  display: block;
}
.lastAddModal>button {
  background-color: #ffffff;
  border: 1px solid #666666;
  border-radius: 6px;
  padding: 5px 7px;
  cursor: pointer;
}
.lastAddInput {
  width: 73%;
  border: none;
  border-bottom: 1px solid #666666;
  background-color: #ffffff00;
  padding: 5px;
  text-align: center;
  margin-right: 8px;
}